<template>
  <div class="register-center">
    <section class="register-center-banner">
      <div class="login-center-user"><i class="mintui mintui-back return-icon " @click="setting(login)"></i></div>
      <div class="register-text">欢迎使用和享世纪</div>
    </section>
    <section class="register-center-input">
      <div class="input-tel padding-top">
        <img src="/static/image/login/login_icon_account@3x.png"></img><input type="number" v-model="tel" @focus="setColor(0)" @blur="removeColor(0)" placeholder="请输入手机号码" autocomplete="off" />
        <div v-bind:class="[line,focus[0].focus ? lineColor:line]"></div>
      </div>
      <div class="input-password">
        <img src="/static/image/login/sign_icon_verifi_code@3x.png"></img><input type="text" v-model="verifi" v-bind:value="verifi" @focus="setColor(2)" @blur="removeColor(2)" autocomplete="off" placeholder="请输入短信验证码" />
        <div class="code" @click="getCode">{{text}}</div>
        <div v-bind:class="[line,focus[2].focus ? lineColor:line]"></div>
      </div>
      <div class="input-password">
        <img src="/static/image/login/sign_icon_invite_number@3x.png"></img><input type="number" v-model="invite" v-bind:value="invite" @focus="setColor(1)" @blur="removeColor(1)" autocomplete="off" placeholder="请输入邀请码（可不填）" />
        <div v-bind:class="[line,focus[1].focus ? lineColor:line]"></div>
      </div>
      <div class="input-password">
        <img src="/static/image/login/login_icon_password@3x.png"></img><input type="password" v-model="password" v-bind:value="password" @focus="setColor(3)" @blur="removeColor(3)" autocomplete="off" placeholder="请设置登录密码" />
        <div v-bind:class="[line,focus[3].focus ? lineColor:line]"></div>
      </div>
      <div class="setting-protocol">
        <i class="checkbox-regi" :class="{'select-yes':proto}" @click="ischeck"></i>我已阅读并同意<span @click="setting('protocol')">《和享时代用户协议》</span>
      </div>
      <div class="login-in" @click="validate(custom)" :class="{no:!isSubmit,yes:isSubmit}">注册</div>
    </section>
  </div>
</template>

<script>
  import {getCode,postRegister} from '../../service/getData'
  export default {
    data () {
      return {
        tel: '',
        line: 'input-botn',
        lineColor: 'input-botn-color',
        focus: [{
          focus: false,
          name: 'tel'
        },
        {
          focus: false
        },
        {
          focus: false
        },
        {
          focus: false
        }
        ],
        password: '',
        invite: '',
        isSend:false,
        verifi: '',
        custom: 'custom',
        login: 'login',
        text: '获取验证码',
        proto: true,
        isSubmit: false
      }
    },
    created: function () {
      var _this = this
      //从页面传过来的参数（邀请码）,因为扫码时跳转链接到注册页面
      if(_this.$route.query.invite){
        _this.invite = _this.$route.query.invite
      }
    },
    methods: {
      // 是否同意协议
      ischeck: function () {
        if (this.proto) {
          this.proto = false
        } else {
          this.proto = true
        }
      },
      setting: function (val, param) {
        this.$router.push({
          name: val,
          params: {
            setparams: param,
            prev: this.$route.name
          }
        })
      },
      getCode: function () {
      	var _this = this
      	if(this.isSend){
      		_this.$toast('验证码发送中，请稍后')
      		return
      	}
      	if (!/^(\(\d{3,4}\)|\d{3,4}-|\s)?\d{7,14}$/.test(_this.tel)) {
      	  _this.$toast('请输入正确的手机号码')
      	  return
      	}
        this.tempTime = 120
        this.text = '120s'
        this.sendBtnTimer = setInterval(this.time, 1000)
        getCode(this.tel).then(function (rep) {
          _this.$toast('获取验证码成功，请查看手机')
      		_this.isSend = true
          return
        }).catch(function () {
          _this.$toast('获取验证码失败！')
        })
      },
      time: function () {
        this.tempTime--
        if (this.tempTime < 1) {
          this.text = '发送验证码'
          clearInterval(this.sendBtnTimer)
      		this.isSend = false
          return false
        }
        this.text = this.tempTime + 's'
      },
      validate: function (val) {
        var _this = this
        if (!/^(\(\d{3,4}\)|\d{3,4}-|\s)?\d{7,14}$/.test(_this.tel)) {
          _this.$toast('请输入正确的手机号码')
          return
        }
        if (_this.verifi == '') {
          _this.$toast('请输入短信验证码')
          return
        }
        if (_this.password == '') {
          _this.$toast('请设置登录密码')
          return
        }
        postRegister(this.tel, this.invite,this.password,this.verifi).then(function (rep) {
           if(rep.data.code == '1004'){
             _this.$toast('验证码已过期')
             return
           }else if(rep.data.code == '1005'){
             _this.$toast('验证码不正确')
             return
           }else if(rep.data.code == '1006'){
             _this.$toast('账号已存在')
             return
           }else if(rep.data.mobile != null){
             //注册成功后跳转到登录页
             _this.$toast('恭喜！账号注册成功！')
             _this.$router.push({
                name: 'login'
             })
           }
        }).catch(function (error) {
            _this.$toast('注册失败，连接服务器异常！')
        })
      },
      setColor: function (item) {
        this.focus[item].focus = true
      },
      removeColor: function (item) {
        this.focus[item].focus = false
      }
    },
    computed: {
      isActive: function () {
        if (this.verifi === ''|| this.tel === '' || this.invite === '' || this.password === '') {
          return false
        } else {
          return true
        }
      }
    },
    watch: {
      isActive: function (newValue,oldValue) {
        this.isSubmit = newValue
      }
    }

  }
</script>
<style>
  /* 注册页面 */
  .register-center-banner {
    height: .91rem;
    position: relative;
    z-index: 1;
    padding: .2rem .125rem .4rem .125rem;
    background: url(/static/image/login/sign_bg@3x.png);
    -webkit-background-size: 100% 100%;
    background-size: 100% 100%;
  }
  
  .register-text {
    position: absolute;
    left: 50%;
    transform: translate(-50%);
    font-size: .20rem;
    color: white;
  }
  
  .register-center-input img {
    width: .18rem;
    height: .18rem;
  }
  
  .padding-top {
    padding: .41rem .36rem .0rem .36rem;
  }
  
  .code {
    width: 32%;
    line-height: normal;
    line-height: .34rem!important;
    vertical-align: middle;
    color: rgb(255, 161, 38);
    text-align: center;
    border-radius: .3rem;
    border: .01rem solid rgb(255, 161, 38);
    transform: translateY(-100%);
    float: right;
  }
  
  .agreement {
    color: #ff9b24;
    position: absolute;
    right: 8%;
    margin-top: .9rem;
  }
  .input-tel {
    width: 100%;
    padding: .70rem .36rem .0rem .36rem;
  }
    
  .input-tel input,
  .input-password input {
    width: 90%;
    transform: translate(.05rem, -20%);
    -webkit-box-shadow: 0 0 0px 1000px white inset;
    background-color: #f5f5f5!important;
  }
  
  .input-botn {
    width: 100%;
    height: .01rem;
    background-color: rgba(6, 11, 0, .1);
    margin-top: .1rem;
  }
  
  .input-botn-color {
    background-color: rgba(255, 139, 32, .6);
  }
  
  .input-password {
    width: 100%;
    padding: .27rem .36rem .0rem .36rem;
  }
  
  .login-in {
    position: absolute;
    width: 80%;
    height: 7%;
    line-height: .45rem;
    left: 50%;
    margin-top: .3rem;
    text-align: center;
    color: rgb(255, 161, 38);
    transform: translateX(-50%);
    border-radius: .3rem;
    color: white;
  }
  .no {
    background: url(/static/image/login/login_bar_dis@3x.png) no-repeat;
    background-size: 100% 100%;
  }
  .yes{
    background: url(/static/image/login/login_bar_nor@3x.png) no-repeat;
    background-size: 100% 100%;
  }
  .setting-protocol {
    padding: .05rem .36rem;
  }
  .setting-protocol span{
    color: rgb(255, 161, 38);
    font-size: .12rem;
  }
  .checkbox-regi {
    position: relative;
    display: inline-block;
    width: .16rem;
    height: .16rem;
    border:.01rem solid rgb(255, 161, 38);
    vertical-align: middle;
    margin-right: .05rem;
    border-radius: 50%;
  }
  .checkbox-regi.select-yes:before {
    position: absolute;
    content: '';
    height: .13rem;
    width: .13rem;
    background-color: rgb(255, 161, 38);
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    border-radius: 50%;
  }
</style>


